@import url("https://fonts.googleapis.com/css2?family=Nanum+Gothic:wght@400;700;800&display=swap");

html {
  font-size: 100%; /* 16px; */
  line-height: 1.5em;
  font-family: "Nanum Gothic", sans-serif;
}

body {
  background-color: #d6d6d6;
  background-position-x: -60px;
  background-size: 1634px;
  margin: 0;
}

header {
  padding: 100px;
  background-color: #fff;
  box-shadow: rgba(0, 0, 0, 0.281) 0px 1px 4px;
}

header .overlaySpacing {
  max-width: 1000px;
  margin: -46px auto 30px auto;
}

.center {
  text-align: center;
}

h1 {
  font-size: 6rem;
  line-height: 7rem;
  margin: 0 auto;
}

.tagline {
  width: auto;
  font-size: 2rem;
  margin: 1rem auto;
  line-height: 4rem;
}

.intro {
  font-size: 1.2rem;
  line-height: 2rem;
}

h1,
h2,
h3 {
  font-weight: 800;
}

h2 {
  font-size: 2.4rem;
  margin-top: 3rem;
}

h3 {
  margin-top: 4rem;
  margin-bottom: 0;
}

p {
  color: #333;
  width: 580px;
  margin: 40px auto;
}

button,
label {
  margin: 35px 0 30px 0;
  padding: 15px 25px;
  background-color: rgb(224, 28, 28);
  border-radius: 10px;
  border: none;
  color: #fff;
  font-size: 1rem;
  cursor: pointer;
}

button.expand {
  display: flex;
  align-items: center;
  color: #000;
  background: none;
  margin: 2rem auto;
}

label {
  display: block;
}

.chevron {
  transition-duration: 200ms;
}

.chevron.right {
  transform: rotate(0);
}
.chevron.down {
  transform: rotate(90deg);
}

button:hover,
label:hover {
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.buttons {
  display: flex;
  justify-content: center;
}

select {
  padding: 10px;
  font-size: 1rem;
  border-radius: 15px;
}

.guide {
  transition: visibility 0s, opacity 0.5s linear;
}

.guide.visible {
  visibility: visible;
  opacity: 1;
}

.guide.hidden {
  visibility: hidden;
  opacity: 0;
  position: absolute;
}